// implement a component here
import './Tabs.css';

import classNames from 'classnames';

interface Props {
  tabs: Tab[],
  onTabSelected: (id: string) => void,
  selectedTab: Tab | null
}

export const Tabs: React.FC<Props> = ({ tabs, onTabSelected, selectedTab }) => {
  const selectedOrNot = (tab: Tab) => {
    if (selectedTab === null) {
      return false;
    }

    return tab.id === selectedTab.id;
  };

  return (
    <ul>
      {tabs.map((tab: Tab) => (
        <li
          key={tab.id}
          className={classNames('Tab', { activ: selectedOrNot(tab) })}
        >
          <title>{tab.title}</title>
          <p data-cy="tab-content">{tab.content}</p>
          <button
            type="button"
            onClick={() => {
              onTabSelected(tab.id);
            }}
          >
            Select
          </button>
        </li>
      ))}
    </ul>
  );
};
